        body {
            text-align: center;
            position: relative;
        }
        
        .search-form {
            margin-top: 40px;
            margin-bottom: 40px;
        }
        
        .gallery {
            display: flex;
            flex-wrap: wrap;
            margin: -15px;
        }
        
        .card {
            width: calc((100% - 90px) / 3);
            list-style: none;
            margin: 15px;
            border: 2px;
            border-radius: 4px;
            border: medium solid grey;
        }
        
        .small-img {
            width: 100%;
            height: 270px;
        }
        
        .load-more-btn {
            margin-top: 40px;
            margin-bottom: 40px;
            background-color: blue;
            color: white;
        }
        
        .is-hidden {
            display: none;
        }
        
        .stats {
            display: flex;
            justify-content: space-between;
        }
        
        .large-img {
            display: none;
        }
        
        .basicLightbox--visible {
            display: flex;
            justify-content: center;
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            padding-top: 30px;
            padding-bottom: 30px;
            padding-left: auto;
            padding-right: auto;
            background-color: rgba($color: #000000, $alpha: 0.6);
        }
        
        .basicLightbox__placeholder {
            margin-top: auto;
            margin-bottom: auto;
        }